import {nameInput, jobInput } from "../pages/index.js";

export default class UserInfo {
    constructor(name, job){
        this._name = name;
        this._job = job;
    }

    //public method, returns object with info about the user 
    getUserInfo(){
        return this._userName = {name: this._name.textContent, job: this._job.textContent};
    }

    //public method, takes new user data and adds to the page
    setUserInfo(name, job){
        this._userInfo = { name, job }
        nameInput.textContent = this._userInfo.name;
        jobInput.textContent = this._userInfo.job;
    }
}