import Popup from "./Popup.js";

export default class PopupWithForm extends Popup {
    constructor({popupSelector, popupSubmit}){
        super(popupSelector);
        this._popupSubmit = popupSubmit;
        this._popupElement = this._popupSelector.querySelector('.popup__form');
    }
    
    //private method, collects data from all input fields
    _getInputValues() {
        // const inputs = Array.from(this._form.querySelectorAll(this._inputSelector));
        // inputs.forEach(inputElement => {
        //      inputElement.addEventListener("input", () => {
        //          //check Input Validity
        //          this._checkInputValidity(inputElement, this._inputErrorClass);
        //          //toggle button state
        //          this._toggleButtonState(this._form, this._inputSelector);
        //          });
        //     });
        this._inputList = this._popupElement.querySelectorAll('.popup__input');
        this._inputValues = {};
        this._inputList.forEach(input => this._inputValues[input.name] = input.value);
        return this._inputValues;
    }

    setEventListeners(){
        this._popupElement.addEventListener('submit', (e) => {
            e.preventDefault();
            this._popupSubmit(this._getInputValues());
            }); 
        super.setEventListeners();
    }

    close(){
        this._form.reset();
        super.close();
    }
}
